@import url("https://fonts.googleapis.com/css2?family=Cabin&family=Montserrat&family=Open+Sans&family=Roboto&family=Sacramento&family=Caveat&family=Architects+Daughter&family=Indie+Flower&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

:root {
  --bgcolor: #f0f0f0;
  --bgcolor-400: #d2d2d2;
  --bgcolor-100: #e1e1e1;
  --bgcolor-700: #c3c3c3;
  --tcolor: #121212;
  --tcolor-100: #333333;
  --tcolor-400: #272727;
  --tcolor-700: #1d1d1d;
  --back-shadow: rgba(0, 0, 0, 0.2);
  --back-shadow-light: rgba(0, 0, 0, 0.1);
  --transition-time: 200ms;
  --red: #f44336;
  --red-100: #ffcdd2;
  --red-400: #ef5350;
  --red-700: #d32f2f;
  --pink: #e91e63;
  --pink-100: #f8bbd0;
  --pink-400: #ec407a;
  --pink-700: #c2185b;
  --purple: #9c27b0;
  --purple-100: #e1bee7;
  --purple-400: #ab47bc;
  --purple-700: #7b1fa2;
  --dark-purple: #673ab7;
  --dark-purple-100: #d1c4e9;
  --dark-purple-400: #7e57c2;
  --dark-purple-700: #512da8;
  --indigo: #3f51b5;
  --indigo-100: #c5cae9;
  --indigo-400: #5c6bc0;
  --indigo-700: #303f9f;
  --blue: #2196f3;
  --blue-100: #bbdefb;
  --blue-400: #42a5f5;
  --blue-700: #1976d2;
  --light-blue: #03a9f4;
  --light-blue-100: #b3e5fc;
  --light-blue-400: #29b6f6;
  --light-blue-700: #0288d1;
  --cyan: #00bcd4;
  --cyan-100: #b2ebf2;
  --cyan-400: #26c6da;
  --cyan-700: #0097a7;
  --green: #4caf50;
  --green-100: #c8e6c9;
  --green-400: #66bb6a;
  --green-700: #388e3c;
  --light-green: #8bc34a;
  --light-green-100: #dcedc8;
  --light-green-400: #9ccc65;
  --light-green-700: #689f38;
  --yellow: #ffeb3b;
  --yellow-100: #fff9c4;
  --yellow-400: #ffee58;
  --yellow-700: #fbc02d;
  --orange: #ff9800;
  --orange-100: #ffe0b2;
  --orange-400: #ffa726;
  --orange-700: #f57c00;
  --deep-orange: #ff5722;
  --deep-orange-100: #ffccbc;
  --deep-orange-400: #ff7043;
  --deep-orange-700: #e64a19;
  --brown: #795548;
  --brown-100: #d7ccc8;
  --brown-400: #8d6e63;
  --brown-700: #5d4037;
  --grey: #9e9e9e;
  --grey-100: #f5f5f5;
  --grey-400: #bdbdbd;
  --grey-700: #616161;
  --blue-grey: #607d8b;
  --blue-grey-100: #cfd8dc;
  --blue-grey-400: #78909c;
  --blue-grey-700: #455a64;
  --blue: #4285f4;
  --red: #ea4335;
  --yellow: #fbbc05;
  --green: #34a853;
}

.light {
  --bgcolor: #f0f0f0;
  --bgcolor-100: #e1e1e1;
  --bgcolor-400: #d2d2d2;
  --bgcolor-700: #c3c3c3;
  --tcolor: #121212;
  --tcolor-100: #333333;
  --tcolor-400: #272727;
  --tcolor-700: #1d1d1d;
  --back-shadow: rgba(0, 0, 0, 0.2);
  --back-shadow-light: rgba(0, 0, 0, 0.1);
}

.dark {
  --bgcolor: #121212;
  --bgcolor-100: #333333;
  --bgcolor-400: #272727;
  --bgcolor-700: #1d1d1d;
  --tcolor: #f0f0f0;
  --tcolor-100: #e1e1e1;
  --tcolor-400: #d2d2d2;
  --tcolor-700: #c3c3c3;
  --back-shadow: rgba(255, 255, 255, 0.2);
  --back-shadow-light: rgba(255, 255, 255, 0.1);
}

.row {
  width: 100%;
  max-height: 100%;
  display: flex;
  flex-wrap: wrap;
}
.row > * {
  width: 100%;
}

[class^=col] {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

@media (min-width: 880px) {
  .row .col-lg-100 {
    width: 100%;
  }
  .row .col-lg-50 {
    width: 50%;
  }
  .row .col-lg-33 {
    width: 33.333%;
  }
  .row .col-lg-25 {
    width: 25%;
  }
}
@media (min-width: 672px) and (max-width: 879.98px) {
  .row .col-md-100 {
    width: 100%;
  }
  .row .col-md-50 {
    width: 50%;
  }
  .row .col-md-33 {
    width: 33.333%;
  }
  .row .col-md-25 {
    width: 25%;
  }
}
@media (max-width: 671.98px) {
  .row .col-sm-100 {
    width: 100%;
  }
  .row .col-sm-50 {
    width: 50%;
  }
  .row .col-sm-33 {
    width: 33.333%;
  }
  .row .col-sm-25 {
    width: 25%;
  }
}
html {
  font-size: 16px;
}
@media (max-width: 672px) {
  html {
    font-size: 12px;
  }
}

body {
  font-family: "Choco cooky", "Google Sans", Cabin, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 1rem;
  background-color: var(--bgcolor);
  color: var(--tcolor);
  width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-flow: column;
  letter-spacing: 0.025rem;
  word-spacing: 0.25rem;
  line-height: 1.25rem;
  transition: all var(--transition-time) ease-in-out;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: var(--tcolor);
}
a:hover, a:active, a:focus, a:visited {
  border: none;
  outline: none;
}

img {
  pointer-events: none;
}

input,
textarea {
  font: inherit;
  text-rendering: auto;
  background-color: var(--bgcolor);
  color: var(--tcolor);
  letter-spacing: normal;
  word-spacing: normal;
  line-height: normal;
  text-transform: inherit;
  text-indent: inherit;
  text-shadow: inherit;
  display: inline-block;
  text-align: start;
  appearance: auto;
  cursor: text;
  padding: 0.5rem;
}
input, input:hover, input:focus, input:active, input:focus-within, input:focus-visible,
textarea,
textarea:hover,
textarea:focus,
textarea:active,
textarea:focus-within,
textarea:focus-visible {
  border: none;
  outline: none;
}

input,
textarea {
  border: none;
  outline: 3px solid transparent;
  border-radius: 4px;
  background-color: var(--bgcolor-400);
  margin: 0.5rem;
  padding: 0.5rem;
  transition: all var(--transition-time) ease-in-out;
  resize: none;
}
input:hover,
textarea:hover {
  border: none;
  outline: 3px solid transparent;
}
input:active, input:focus,
textarea:active,
textarea:focus {
  border: none;
  outline: 3px solid rgba(0, 0, 0, 0.2);
}
input::placeholder,
textarea::placeholder {
  font-size: 0.875rem;
}

button {
  background: none;
  color: inherit;
  border: none;
  outline: none;
  font: inherit;
  text-decoration: inherit;
  text-transform: inherit;
  text-shadow: inherit;
  text-rendering: inherit;
  text-size-adjust: inherit;
  text-indent: inherit;
  text-align: inherit;
  appearance: inherit;
}
button:hover, button:active, button:focus, button:focus-visible, button:focus-within {
  border: none;
  outline: none;
}

.btn {
  margin: 0.25rem 0.5rem;
  padding: 0.825rem 1.275rem;
  background-color: transparent;
  border: 5px solid rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.25rem;
  font-weight: 550;
  cursor: pointer;
  user-select: none;
  line-height: 1.5;
  transition: all var(--transition-time) ease-in-out;
  background-color: var(--indigo);
  color: var(--bgcolor);
  position: relative;
  overflow: hidden;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12);
}
@media (max-width: 880px) {
  .btn {
    padding: 0.75rem 1rem;
  }
}
@media (max-width: 672px) {
  .btn {
    width: 0.45rem 0.875rem;
  }
}
.btn::before {
  content: "";
  position: absolute;
  top: 5%;
  left: 50%;
  width: 2.75rem;
  height: 3rem;
  border-radius: 15px 25px 20px 17.5px;
  background-color: rgba(255, 255, 255, 0.3);
  z-index: 0;
  transition: all var(--transition-time) ease-in-out;
  animation: wavy 10s linear infinite;
}
@media (max-width: 672px) {
  .btn {
    padding: 0.6rem 0.75rem;
  }
}
.btn-sm {
  font-size: 0.875rem;
  padding: 0.6rem 1rem;
  font-weight: 400;
}
.btn-lg {
  font-size: 1.125rem;
  padding: 1rem 1.6rem;
  font-weight: 550;
}
.btn:hover {
  border: 5px solid rgba(255, 255, 255, 0.1);
}
.btn:hover::before {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.1);
  top: 0;
  left: 0;
  border-radius: 0;
  animation: none;
}
.btn:active, .btn:focus {
  border: 5px solid rgba(0, 0, 0, 0.5);
}

@keyframes wavy {
  0% {
    transform: rotate(0deg);
    left: 0%;
  }
  10% {
    transform: rotate(36deg);
    left: 16%;
  }
  20% {
    transform: rotate(72deg);
    left: 32%;
  }
  30% {
    transform: rotate(108deg);
    left: 48%;
  }
  40% {
    transform: rotate(144deg);
    left: 64%;
  }
  50% {
    transform: rotate(180deg);
    left: 80%;
  }
  60% {
    transform: rotate(216deg);
    left: 64%;
  }
  70% {
    transform: rotate(252deg);
    left: 48%;
  }
  80% {
    transform: rotate(288deg);
    left: 32%;
  }
  90% {
    transform: rotate(234deg);
    left: 16%;
  }
  100% {
    transform: rotate(360deg);
    left: 0%;
  }
}
.fab {
  background-color: var(--tcolor-400);
  color: #f0f0f0;
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  z-index: 2000;
  border: none;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 0;
  height: 3.5rem;
  width: 3.5rem;
  overflow: visible;
}
.fab, .fab .material-icons {
  transition: all var(--transition-time) ease-in-out;
}
.fab:hover {
  background-color: var(--bgcolor-100);
  color: #121212;
}
.fab:active, .fab:focus {
  outline: none;
  background-color: var(--tcolor-700);
  color: #f0f0f0;
}

.snackbar {
  background-color: var(--tcolor-400);
  color: var(--bgcolor);
  position: fixed;
  bottom: 2rem;
  left: 2rem;
  z-index: 2000;
  border-radius: 5px;
  padding: 0.5rem 0.25rem;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  min-width: 5rem;
  height: 3.5rem;
  overflow: hidden;
  transform: all var(--transition-time) ease-in-out;
}
.snackbar-text {
  width: 100%;
  height: 100%;
  padding: 0.5rem 2rem 0.5rem 1rem;
  background: transparent;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.contact {
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: darken;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact-box {
  width: 85%;
  height: 95%;
  clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--brown);
  position: relative;
}
@media (max-width: 880px) {
  .contact-box {
    clip-path: none;
  }
}
.contact-side {
  background: inherit;
  height: 100%;
  transition: all 0.8s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  backface-visibility: hidden;
  border-radius: 3px;
  overflow: hidden;
  box-shadow: 0 1.5rem 40px rgba(18, 18, 18, 0.15);
}
.contact-side-back {
  transform: rotateY(180deg);
}
.contact-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-flow: column;
}
.contact-head {
  width: 100%;
  height: 10%;
  font-family: Montserrat, sans-serif;
  color: var(--bgcolor);
  text-align: center;
  font-size: 4rem;
  line-height: 4rem;
  padding: 1rem 0;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact-head-me {
  width: 60%;
  height: 40%;
}
@media (max-width: 880px) {
  .contact-head {
    width: 85%;
  }
}
@media (max-width: 672px) {
  .contact-head {
    flex-flow: column;
    width: 95%;
  }
}
.contact-head-image {
  width: 35%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact-head-image__img {
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
}
@media (max-width: 672px) {
  .contact-head-image {
    width: 100%;
    height: 50%;
  }
}
.contact-head-content {
  width: 65%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}
.contact-head-content > * {
  width: 100%;
  height: 35%;
  font-size: 2rem;
  line-height: 2.5rem;
  padding: 1.5rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media (max-width: 880px) {
  .contact-head-content > * {
    font-size: 1.75rem;
  }
}
@media (max-width: 672px) {
  .contact-head-content > * {
    width: 95%;
  }
}
.contact-head-content__name {
  font-size: 3rem;
}
@media (max-width: 880px) {
  .contact-head-content__name {
    font-size: 2.5rem;
  }
}
@media (max-width: 672px) {
  .contact-head-content__name {
    width: 95%;
  }
}
@media (max-width: 672px) {
  .contact-head-content {
    width: 100%;
    height: 50%;
  }
}
.contact-body {
  width: 70%;
  height: 60%;
}
@media (max-width: 880px) {
  .contact-body {
    width: 90%;
  }
}
@media (max-width: 672px) {
  .contact-body {
    width: 95%;
    overflow-y: auto;
  }
}
.contact-social {
  width: 100%;
  height: 100%;
  padding: 0.5rem 1.5rem;
}
@media (max-width: 880px) {
  .contact-social {
    padding: 0.25rem 0.75rem;
  }
}
@media (max-width: 672px) {
  .contact-social {
    width: 95%;
  }
}
.contact-social-image {
  width: 15%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 880px) {
  .contact-social-image {
    width: 10%;
  }
}
@media (max-width: 672px) {
  .contact-social-image {
    width: 7.5%;
  }
}
.contact-social-image img {
  object-fit: cover;
  width: 2rem;
  height: 2rem;
  filter: invert(1);
}
@media (max-width: 880px) {
  .contact-social-image img {
    width: 1.5rem;
    height: 1.5rem;
  }
}
@media (max-width: 672px) {
  .contact-social-image img {
    width: 1.25rem;
    height: 1.25rem;
  }
}
.contact-social-content {
  width: 85%;
  height: 100%;
  font-size: 1.1rem;
  padding: 0 0.75rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media (max-width: 880px) {
  .contact-social-content {
    padding: 0 0.5rem;
    font-size: 1rem;
  }
}
.contact-form {
  width: 75%;
  height: 80%;
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  align-items: center;
}
@media (max-width: 880px) {
  .contact-form {
    width: 85%;
  }
}
@media (max-width: 672px) {
  .contact-form {
    width: 95%;
  }
}
.contact-form input, .contact-form textarea {
  width: 75%;
  background-color: rgba(255, 255, 255, 0.25);
  color: var(--bgcolor);
  border-radius: 0;
}
.contact-form input::placeholder, .contact-form textarea::placeholder {
  color: var(--bgcolor-700);
  font-size: 1rem;
}
@media (max-width: 880px) {
  .contact-form input, .contact-form textarea {
    width: 85%;
  }
}
@media (max-width: 672px) {
  .contact-form input, .contact-form textarea {
    width: 95%;
  }
}
.contact-form input {
  height: 10%;
}
@media (max-width: 880px) {
  .contact-form input {
    height: 7.5%;
  }
}
@media (max-width: 672px) {
  .contact-form input {
    height: 5%;
  }
}
.contact-form textarea {
  height: 45%;
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: rgba(0, 0, 0, 0.01);
  border: 4px solid transparent;
}
::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0.2);
}
::-webkit-scrollbar-thumb {
  background-color: #7a7a7a;
  border-radius: 500px;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #525252;
}

.material-icons {
  user-select: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

